//======================================================================
// Variables
//======================================================================

// Colors
$primary-color: #007bff;
$secondary-color: #6c757d;
$success-color: #28a745;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #17a2b8;

$text-primary: #495057;
$text-secondary: #6c757d;
$border-color: #dee2e6;
$border-color-light: #e9ecef;
$background-light: #f8f9fa;
$white: #fff;
$black: #212529;

// Spacing
$spacing-unit: 0.25rem;
$base-padding: 1rem;
$base-margin: 1.5rem;

// Typography
$font-size-base: 1rem;
$font-size-sm: 0.875rem;
$font-size-xs: 0.75rem;
$font-size-lg: 1.125rem;
$font-size-xl: 1.25rem;
$font-weight-normal: 400;
$font-weight-bold: 600;

// Borders & Radii
$border-radius: 0.375rem;
$border-radius-sm: 0.25rem;

//======================================================================
// Mixins
//======================================================================

@mixin badge-variant($bg, $color, $border) {
  background-color: $bg;
  color: $color;
  border: 1px solid $border;
}

@mixin button-variant($bg, $border, $color, $hover-bg, $hover-border) {
  background-color: $bg;
  border-color: $border;
  color: $color;

  &:hover {
    background-color: $hover-bg;
    border-color: $hover-border;
  }
}

//======================================================================
// Main Styles
//======================================================================

/* Transaction Details Modal */
.transaction-details {
  max-width: 100%;
  padding: 0;
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $base-margin;
  padding-bottom: $base-padding;
  border-bottom: 2px solid $border-color-light;

  h3 {
    margin: 0;
    display: flex;
    align-items: center;
    gap: $spacing-unit * 2;
    color: $text-primary;
    font-size: $font-size-xl;
  }

  .status-badge {
    font-size: $font-size-sm;
    padding: ($spacing-unit * 1.5) ($spacing-unit * 3);
  }

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: flex-start;
    gap: $spacing-unit * 3;
  }
}

/* Details Grid Layout */
.details-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: $base-margin;
  margin-bottom: $base-margin;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: $base-padding;
  }
}

.detail-section {
  background: $background-light;
  border: 1px solid $border-color-light;
  border-radius: $border-radius;
  padding: $base-padding;

  h4 {
    margin: 0 0 ($spacing-unit * 3) 0;
    color: $text-primary;
    font-size: $font-size-base;
    display: flex;
    align-items: center;
    gap: $spacing-unit * 2;

    .material-symbols-outlined {
      font-size: $font-size-lg;
      color: $text-secondary;
    }
  }

  @media print {
    break-inside: avoid;
    background: transparent !important;
    border: 1px solid $black !important;
  }
}

/* Info Grid */
.info-grid {
  display: grid;
  gap: $spacing-unit * 2;

  p {
    margin: 0;
    padding: $spacing-unit 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  strong {
    color: $text-primary;
    margin-right: $spacing-unit * 2;
    flex-shrink: 0;
  }
}

/* Amount Details */
.amount-details {
  background: $white;
  border: 1px solid $border-color;
  border-radius: $border-radius-sm;
  padding: $spacing-unit * 3;
}

.amount-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-unit 0;
  border-bottom: 1px solid $border-color-light;

  &:last-child {
    border-bottom: none;
  }

  &.total {
    border-top: 2px solid $text-primary;
    margin-top: $spacing-unit * 2;
    padding-top: $spacing-unit * 2;
    font-weight: $font-weight-bold;
    font-size: $font-size-lg;

    strong {
      color: $primary-color;
    }
  }

  strong {
    color: $success-color;
    font-weight: 600;
  }
  
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: flex-start;
    gap: $spacing-unit;
  }
}

/* Items Table */
.table-responsive {
  background: $white;
  border-radius: $border-radius-sm;
  border: 1px solid $border-color;
  overflow-x: auto;
}

.table {
  margin-bottom: 0;
  width: 100%; // Ensure table takes full width
  border-collapse: collapse; // Better borders

  th {
    background: $background-light;
    border-bottom: 2px solid $border-color;
    font-weight: $font-weight-bold;
    color: $text-primary;
    font-size: $font-size-sm;
    padding: $spacing-unit * 3; // Add padding
    text-align: left; // Align text
  }

  td {
    vertical-align: middle;
    border-top: 1px solid $border-color;
    padding: $spacing-unit * 3; // Add padding
  }
}

.table-totals td {
  background: $background-light;
  font-weight: $font-weight-bold;
  border-top: 2px solid $text-primary;
}

.table-totals:first-child td {
  border-top: 1px solid $border-color;
}

/* SmartBill Info */
.smartbill-info {
  background: $white;
  border: 1px solid $border-color;
  border-radius: $border-radius-sm;
  padding: $spacing-unit * 3;

  p {
    margin: 0 0 ($spacing-unit * 2) 0;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

/* Generic Badge & Status/Type Badges */
.badge, .status-badge, .type-badge {
  display: inline-flex;
  align-items: center;
  gap: $spacing-unit;
  font-weight: $font-weight-bold;
  border-radius: $border-radius-sm;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border: 1px solid;
}

.badge {
  padding: $spacing-unit ($spacing-unit * 2);
  font-size: $font-size-xs;
}

.status-badge, .type-badge {
    padding: $spacing-unit ($spacing-unit * 2);
    font-size: $font-size-xs;
    border-radius: $border-radius;
}

.badge-success, .status-completed {
  @include badge-variant(#d4edda, #155724, #c3e6cb);
}
.badge-warning, .status-pending, .type-adjustment {
  @include badge-variant(#fff3cd, #856404, #ffeaa7);
}
.badge-danger, .status-failed, .type-purchase {
  @include badge-variant(#f8d7da, #721c24, #f5c6cb);
}
.status-processing {
  @include badge-variant(#cce7ff, #004085, #99d3ff);
}
.status-cancelled, .type-transfer {
  @include badge-variant(#e2e3e5, #383d41, #d6d8db);
}
.type-sales {
  @include badge-variant(#d1ecf1, #0c5460, #bee5eb);
}
.type-return {
  @include badge-variant(#f1b6d2, #6f2c63, #ec9bca);
}

/* Audit Trail */
.audit-trail {
  max-height: 300px;
  overflow-y: auto;
  background: $white;
  border: 1px solid $border-color;
  border-radius: $border-radius-sm;
  padding: $spacing-unit * 3;

  @media print {
    max-height: none;
    overflow: visible;
  }
}

.audit-entry {
  padding: $spacing-unit * 3;
  border-bottom: 1px solid $border-color-light;
  margin-bottom: $spacing-unit * 3;

  &:last-child {
    border-bottom: none;
    margin-bottom: 0;
  }
}

.audit-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $spacing-unit * 2;

  strong {
    color: $text-primary;
    text-transform: capitalize;
  }
}

.audit-time {
  font-size: $font-size-xs;
  color: $text-secondary;
  font-weight: $font-weight-normal;
}

.audit-details {
  color: $text-secondary;
  font-size: $font-size-sm;
  line-height: 1.4;
}

/* Details Actions */
.details-actions {
  margin-top: $base-margin;
  padding-top: $base-padding;
  border-top: 1px solid $border-color-light;
  display: flex;
  gap: $spacing-unit * 3;
  flex-wrap: wrap;
  justify-content: flex-end;

  .btn {
    display: flex;
    align-items: center;
    gap: $spacing-unit * 2;
    padding: ($spacing-unit * 2) ($spacing-unit * 4);
    border-radius: $border-radius;
    border: 1px solid;
    cursor: pointer;
    text-decoration: none;
    font-size: $font-size-sm;
    font-weight: 500;
    transition: all 0.2s ease;
  }
  
  @media (max-width: 768px) {
      justify-content: center;
  }
  
  @media print {
      display: none;
  }
}

// Button variants
.btn-primary {
  @include button-variant($primary-color, $primary-color, $white, #0069d9, #0062cc);
}
.btn-secondary {
  @include button-variant($secondary-color, $secondary-color, $white, #5a6268, #545b62);
}
.btn-warning {
  @include button-variant($warning-color, $warning-color, $black, #e0a800, #d39e00);
}
.btn-danger {
  @include button-variant($danger-color, $danger-color, $white, #c82333, #bd2130);
}

/* Loading and Error States */
.loading-state, .error-state {
  text-align: center;
  padding: $base-padding * 2;
  color: $text-secondary;

  .material-symbols-outlined {
    font-size: 2rem;
    margin-bottom: $spacing-unit * 2;
  }
}

.loading-state .material-symbols-outlined {
  color: $primary-color;
}

.error-state .material-symbols-outlined {
  color: $danger-color;
}

.rotating {
  animation: rotate 1s linear infinite;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}